@import "variables";

.fs-notice.fs-slug-independent-analytics {
    color: $color-gray-16 !important;
    border-left-color: $color-purple;

    .fs-plugin-title {
        top: 0;
        right: auto;
        bottom: auto;
        left: 0;
        border-radius: 0;
        background-color: $color-purple;
    }
    .fs-notice-body {
        margin: 28px 0 12px;
    }
    .fs-close {
        color: $color-gray-45;

        span {
            margin-left: -3px;
        }
    }
}